import React, {Component} from 'react';
import Subject from './components/Subject';

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      subject: {
        title: "JIBAE's",
        sub: 'Blog',
      },
    }
  }
  render() {
    return(
      <div className="App">
          <Subject 
            title={this.state.subject.title}
            sub={this.state.subject.sub}
          />
      </div>
    )};
}

export default App;
